<template>
  <el-card class="box-card">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="username">
        <el-input placeholder="username" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="taxid">
        <el-input placeholder="taxid" v-model="param.taxid"></el-input>
      </el-form-item>
      <el-form-item label="salary">
        <el-input type="number" placeholder="salary" v-model="param.salary"></el-input>
      </el-form-item>
      <el-form-item label="hired">
        <el-date-picker  value-format="yyyy-MM-dd"  placeholder="hired" v-model="param.hired"></el-date-picker>
      </el-form-item>
      <el-form-item label="employeeExperience">
        <el-input type="number" placeholder="employeeExperience" v-model="param.employeeExperience"></el-input>
      </el-form-item>
      <el-form-item label="licenseid">
        <el-input placeholder="licenseid" v-model="param.licenseid"></el-input>
      </el-form-item>
      <el-form-item label="pilotingExperience">
        <el-input placeholder="pilotingExperience" v-model="param.pilotingExperience"></el-input>
      </el-form-item>
      <el-form-item label="managerStatus">
        <el-input placeholder="managerStatus" v-model="param.managerStatus"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="taxid"
          label="taxid"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="salary"
          label="salary"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="hired"
          label="hired"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="employeeExperience"
          label="employeeExperience"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="licenseid"
          label="licenseid"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="pilotingExperience"
          label="pilotingExperience"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="managerStatus"
          label="managerStatus"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        username: '',
        taxid: '',
        hired: '',
        salary: '',
        employeeExperience: '',
        licenseid: '',
        pilotingExperience: '',
        managerStatus: ''
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList(){
      this.$axios.post('/displayEmployeeView/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm(){
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e){
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.el-pagination {
  margin-top: 10px;
}
</style>
